<template>
  <div class="card-container" :style="containerStyle">
    <div v-for="({ key: groupKey, value: cardGroup }, index) in sortedCardList" :key="groupKey" class="card-group">
      <div class="group-title">
        {{ Number(groupKey).toFixed(1) }}
      </div>
      <div class="card-grid" :style="getGridStyle()">
        <slot 
          v-for="(item, idx) in cardGroup" 
          :key="idx"
          :cardData="item"
          :index="idx"
          :groupKey="groupKey"
          :loadImage="loadImage"
        ></slot>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed, onUnmounted, ref } from 'vue';

const props = defineProps({
  cardList: {
    type: Object,
    required: true
  },
  columns: {
    type: Number,
    required: true
  },
  gap: {
    type: Number,
    default: 16
  },
  gridWidth: {
    type: [Number, String],
    default: 1500
  },
  lazyThreshold: {
    type: Number,
    default: 0
  }
});

const observer = ref(null);

const containerStyle = computed(() => {
  return {
    width: typeof props.gridWidth === 'number' ? `${props.gridWidth}px` : props.gridWidth,
    margin: '0 auto',
    boxSizing: 'border-box'
  };
});

const getGridStyle = () => {
  return {
    '--column-count': props.columns,
    '--gap-size': `${props.gap}px`,
    '--column-width': `calc((100% - (var(--column-count) - 1) * var(--gap-size)) / var(--column-count))`
  };
};

const sortedCardList = computed(() => {
  const entries = Object.entries(props.cardList);
  const sortedEntries = entries.sort((a, b) => {
    const numA = Number(a[0]);
    const numB = Number(b[0]);
    return numB - numA;
  });
  return sortedEntries.map(([key, value]) => ({ key, value }));
});

const initObserver = () => {
  if (!window.IntersectionObserver) {
    return (el) => {
      el.dataset.loaded = 'true';
      if (el.tagName === 'IMG') {
        el.src = el.dataset.src;
      }
    };
  }

  observer.value = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const el = entry.target;
        el.dataset.loaded = 'true';
        if (el.tagName === 'IMG' && el.dataset.src) {
          el.src = el.dataset.src;
          el.removeAttribute('data-src');
        }
        observer.value.unobserve(el);
      }
    });
  }, {
    rootMargin: '0px',
    threshold: props.lazyThreshold
  });

  return (el) => {
    if (el && !el.dataset.loaded) {
      observer.value.observe(el);
    }
  };
};

const loadImage = initObserver();

onUnmounted(() => {
  if (observer.value) {
    observer.value.disconnect();
    observer.value = null;
  }
});
</script>

<style scoped>
.card-container {
  width: 100%;
  box-sizing: border-box;
}

.card-group {
  width: 100%;
  margin-bottom: 24px;
  box-sizing: border-box;
}

.group-title {
  height: 30px;
  line-height: 30px;
  background-color: #2c2c2c;
  color: #ffffff;
  padding: 0 16px;
  border-radius: 4px 4px 0 0;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
  box-sizing: border-box;
  text-align: center;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap-size);
  box-sizing: border-box;
  width: 100%;
}

.card-grid > * {
  width: var(--column-width) !important;
  flex-shrink: 0;
  box-sizing: border-box;
}

.card-grid img {
  background-color: #f5f5f5;
  min-height: 100px;
  transition: opacity 0.3s ease;
}

.card-grid img[data-src] {
  opacity: 0;
}

.card-grid img:not([data-src]) {
  opacity: 1;
}
</style>