<template>
  <div></div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'LevelGet',
  methods: {
    async getLevel() {
      const net = '' 
      const net_assets = `${net}/assets`
      const net_files = `${net}/files`
      const difFile = `${net_files}/infos/difficulty.tsv`

      const difColor_map = {
        'EZ': '#0D9A28',
        'HD': '#0175B8',
        'IN': '#CF1313',
        'AT': '#383838',
        'Legacy': '#383838',
        'SP': '#383838'
      }

      try {
        const response = await axios.get(difFile)
        const tsvContent = response.data
        
        const rows = tsvContent.split('\n').filter(row => row.trim())
        const dif = []

        for (const rowStr of rows) {
          const row = rowStr.split('\t').map(cell => cell.trim())
          if (!row || row.length === 0) continue
          if (row[0] && !row[0].endsWith('.0')) continue

          dif.push([row[0], row[1], "EZ"])
          if (row.length > 2) {
            dif.push([row[0], row[2], "HD"])
            dif.push([row[0], row[3], "IN"])
          }
          if (row.length > 4) {
            if (parseFloat(row[4]) > 0) {
              dif.push([row[0], row[4], "AT"])
            }
          }
        }

        const net_assets_ill = net_assets
        for (const item of dif) {
          item[1] = Math.round(parseFloat(item[1]) * 10) / 10
          item.push(item[2])
          item.push(difColor_map[item[2]])
          item[2] = `${net_assets_ill}/illLow/${item[0]}.png`
        }

        const setL = {}
        for (const sub_list of dif) {
          const key = sub_list[1]
          if (!setL[key]) {
            setL[key] = []
          }
          setL[key].push(sub_list)
        }

        const sSetL = {}
        for (const [key, value_list] of Object.entries(setL)) {
          const sorted_items = value_list.sort((a, b) => a[0].localeCompare(b[0]))
          sSetL[key] = sorted_items
        }

        return sSetL
      } catch (error) {
        console.error('获取关卡数据失败：', error)
        console.error('实际请求的TSV文件路径：', difFile)
        return {}
      }
    }
  }
}
</script>

