import { createRouter, createWebHashHistory } from 'vue-router'
import Layout from './Layout.vue'
import startPage from './startPage.vue'
import tableWeb from './tableWeb.vue'

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      {
        path: '',
        name: 'start',
        component: startPage
      },
      {
        path: 'table',
        name: 'table',
        component: tableWeb
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: { name: 'start' }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  }
})

export default router